import React from "react";
import Promo from "./Promo";
const Banner = (props) => {
  let html = "";
  let { type } = props;
  switch (type) {
    case 'promo':
      html = <Promo />;
      break;
    default:
      html = <Promo />;
      break;
  }
  return <>{html}</>;
};

export default Banner;
