import React from "react";
import TotalPayment from "./TotalPayment";
const BottomNav = (props) => {
  let html = "";
  let { type } = props;
  switch (type) {
    case "total_payment":
      html = <TotalPayment />;
      break;
    default:
      html = <TotalPayment />;
      break;
  }
  return <>{html}</>;
};

export default BottomNav;
