import { InputAdornment, TextField } from "@mui/material";
import React from "react";
import NumberFormat from "react-number-format";
import withStyles from "@mui/styles/withStyles";
import makeStyles from "@mui/styles/makeStyles";


const NumberFormatCustom = React.forwardRef(function NumberFormatCustom(
  props,
  ref
) {
  const { onChange, ...other } = props;

  return (
    <NumberFormat
      {...other}
      getInputRef={ref}
      onValueChange={(values) => {
        onChange({
          target: {
            name: props.name,
            value: values.value,
          },
        });
      }}
      format={"####-####-####"}
      allowLeadingZeros
      allowNegative={false}
      decimalScale={0}
    //   maxLength={13}
    //   minLength={10}
    />
  );
});
const PhoneNumberInput = (params) => {
  const { classes, props } = params;

//   const helperText = (
//     <span className={classes.helperText}>{props.helperText}</span>
//   );
//   const errorText = (
//     <span className={classes.helperText}>{props.errorText}</span>
//   );
return (
  <TextField
    fullWidth={props.fullWidth}
    label={props.label}
    value={props.value}
    onChange={props.onChange}
    name={props.name}
    id={props.id}
    InputProps={{
      inputComponent: NumberFormatCustom,
    }}
    error={props.error}
    helperText={props.helperText}
    variant={props.variant || "outlined"}
  />
);
};

const useStyles = makeStyles(() => ({}));

const CustomInput = (props) => {
  let input = null;
  const classes = useStyles();
  switch (props.inputType) {
    case "phone-number":
      input = PhoneNumberInput({ props, classes });
      break;

    default:
      input = PhoneNumberInput({ props, classes });
      break;
  }

  return <> {input} </>;
};

export default CustomInput;
