import React from "react";
import ChevronRight from "@mui/icons-material/ChevronRight";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/Typography";
import Box from "@mui/material/Box";
import makeStyles from "@mui/styles/makeStyles";
import { NTRL_300, PRI_600 } from "../../theme/colors";

const useStyles = makeStyles(() => ({
  unselected: {
    padding: "0 12px",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    borderRadius: "6px",
    borderColor: NTRL_300 + " !important",
    cursor: "pointer",
  },
  disabled: {
    padding: "0 12px",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    borderRadius: "6px",
    borderColor: NTRL_300 + " !important",
  },
  selected: {
    padding: "0 12px",
    display: "flex",
    justifyContent: "space-between",
    alignItems: "center",
    borderRadius: "6px",
    borderColor: PRI_600 + " !important",
    cursor: "pointer",
  },
}));
const PaymentCard = (props) => {
  const classes = useStyles();
  const {
    title = "Undefined",
    price = "Undefined",
    selected = false,
    disabled = true,
  } = props;
  console.log(selected);
  const handleClass = () => {
    if (disabled) {
      return classes.disabled;
    } else if (!selected) {
      return classes.unselected;
    } else {
      return classes.selected;
    }
  };
  return (
    <>
      <Box className={handleClass()} border={2}>
        <div>
          <Typography variant="subtitle2">{title}</Typography>
          <Typography variant="body1">{price}</Typography>
        </div>
        <IconButton>
          <ChevronRight />
        </IconButton>
      </Box>
    </>
  );
};

export default PaymentCard;
