import React from "react";
import Back from "./Back";
const TopNav = (props) => {
  let html = "";
  let { type, title, click } = props;
  switch (type) {
    case "back":
      html = <Back title={title} click={click}/>;
      break;
    default:
      html = <Back title={title} click={click} />;

      break;
  }
  return <>{html}</>;
};

export default TopNav;
